import store from '../store/index';

export default {

    getSum(object) {
        let counters = store.getters.COUNTERS.filter( item => { return item.object_id === object.id });
        let sum = 0;

        for (var i in counters) {
            sum += this.getCounterSum(counters[i]);
        }
        return Math.round(sum* 100) / 100;
    },

    getAllSum(objects) {
        let sum = 0;
        for(var i in objects) {
            sum += this.getSum(objects[i]);
        }

        return Math.round(sum* 100) / 100;
    },

    getCounterValue(counter) {
        return Math.round((parseFloat(counter.values.current_value) - parseFloat(counter.values.payment_value)) * 100) / 100;
    },

    getCounterSum(counter) {
        return Math.round((this.getCounterValue(counter) *  counter.values.tariff[0].recordValues[3]) * 100) / 100;
    }
}
