export default {
    all() {
        return [
            {
                id: 1,
                title: ''
            },
            {
                id: 2,
                title: ' '
            },
            {
                id: 3,
                title: ''
            },
            {
                id: 4,
                title: ''
            }
        ]
    },
    getTitle(id) {
        let item = this.all().filter((item) => item.id === parseInt(id)).pop();
        return item ? '<span class="counter-type type_'+ item.id +'">'+ item.title +'</span>' : null;
    }

}
