<template>
    <div class="row cover">
        <h2 class="big-title">  </h2>
        <form v-if="isPhone" action="/login" method="post" @submit.prevent="authorized">
            <phone-field v-model="login" name="phone" required />
            <button-field class="btn-action" title=" " />
        </form>

        <form v-if="isCode" action="/login" method="post" @submit.prevent="checkCode">
            <text-field v-model="code" type="number" name="code" placeholder="Sms " required />
            <button-field class="btn-action" title="  " />
            <button-field class="btn-action-hollow" @click="showAnotherPhone" title="  " />
        </form>
    </div>
</template>

<script>
    import TextField from '../forms/Text';
    import PhoneField from '../forms/Phone';
    import ButtonField from '../forms/Button';

    export default {
        name: 'Login',
        components: {
            PhoneField,
            ButtonField,
            TextField,
        },
        data: () => ({
            code: '',
            login: '',
            step: 'phone',
        }),
        computed: {
            isPhone() {
                return this.step == 'phone';
            },
            isCode() {
                return this.step == 'code';
            }
        },
        methods: {
            showAnotherPhone() {
                this.login = '';
                this.step = 'phone';
            },
            authorized() {
                if (this.login.replace(/\D/gi, '').length !== 11) {
                    this.$store.commit('ADD_ERROR', '    !');
                } else {
                    // TODO:  
                    this.step = 'code';
                }
            },
            checkCode() {
                this.$store.dispatch('AUTHORIZED', this.login)
                    .then(() => {
                        this.$router.push({ name : 'start' });
                    });
            },
        },
    }
</script>
